<template>
  <div>
    <modal-profesor-nuevo @actualizarTablaProfesores="actualizarTablaProfesores" />
    <modal-reestablecer-password />
    <b-card class="mt-0 px-2">

      <b-table
        id="my-table"
        ref="refTablaProfesoresList"
        class="position-relative"
        show-empty
        head-variant="dark"
        hover
        small
        responsive
        :busy.sync="isBusy"
        :items="myProvider"
        :fields="fields"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
        caption-top
      >
        <template #table-caption>
          <b-row>
            <b-col
              xl="4"
              lg="4"
              md="4"
              sm="12"
            >
              <span class="text-dark"><strong>LISTA DE PROFESORES</strong> </span>
            </b-col>
            <b-col
              xl="8"
              lg="8"
              md="8"
              sm="12"
              class="d-flex justify-content-end"
            >
              <div class="mr-1">
                <label>Mostrar</label>
                <b-form-select
                  v-model="perPage"
                  :options="pageOptions"
                  :clearable="false"
                  size="md"
                  style="width:auto"
                  class="per-page-selector d-inline-block mx-50"
                />
                <label>registros</label>
              </div>
              <div class="mr-1">
                <b-form-input
                  v-model="filter"
                  type="search"
                  placeholder="Buscar..."
                  size="md"
                />
              </div>
              <div>
                <b-button
                  variant="primary"
                  @click="agregarNuevoProfesor"
                >
                  Nuevo profesor DIBU
                </b-button>
              </div>
            </b-col>

          </b-row>
        </template>
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong> cargando...</strong>
          </div>
        </template>
        <template #cell(index)="row">
          {{ from + row.index }}
        </template>

        <template #cell(apellidos_nombres)="row">
          <b-media vertical-align="center">
            <template #aside>
              <b-avatar
                :src="row.item.foto"
                size="32"
                :variant="`light-primary`"
                :text="avatarText(recorteName(row.item.apellidos))"
              />
            </template>
            <b-link
              class="font-weight-bold d-block text-nowrap"
            >
              {{ row.item.apellidos }} {{ row.item.nombres }}
            </b-link>
          </b-media>
        </template>

        <template #cell(estado)="row">

          <b-badge :variant="row.item.activo === 1 ? 'light-success' : 'light-danger'">
            {{ row.item.activo === 1 ? 'activo' : 'inactivo' }}
          </b-badge>

        </template>
        <!-- column: acciones -->
        <template #cell(acciones)="row">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >
            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="18"
                class="align-middle text-primary"
              />
            </template>

            <b-dropdown-item
              @click="editarProfesor(row.item)"
            >
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="restablecerContrasenia(row.item)"
            >
              <feather-icon icon="KeyIcon" />
              <span class="align-middle ml-50">Reestablecer contraseña</span>
            </b-dropdown-item>

          </b-dropdown>
        </template>
      </b-table>

      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>
        </b-row>
      </div>
    </b-card>

  </div>
</template>

<script>
import {
  BCard, BTable, BSpinner, BRow, BCol, BPagination, BFormInput, BDropdown,
  BDropdownItem, BButton, BMedia, BAvatar, BLink, BFormSelect, BBadge,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
import store from '@/store'
import { avatarText } from '@core/utils/filter'
import ModalReestablecerPassword from './ModalReestablecerPassword.vue'
import ModalProfesorNuevo from './ModalProfesorNuevo.vue'

// import ModalNuevaSalida from '@/views/salud/farmacia/salidas-list/ModalNuevaSalida.vue'
// import ModalDetalleSalida from '@/views/salud/farmacia/salidas-list/ModalDetalleSalida.vue'

import profesorStoreModule from './profesorStoreModule'

export default {
  components: {
    BBadge,
    BCard,
    BTable,
    BSpinner,
    BFormSelect,
    BRow,
    BCol,
    BFormInput,
    BDropdown,
    BDropdownItem,
    BPagination,
    BButton,
    BMedia,
    BAvatar,
    BLink,

    ModalProfesorNuevo,
    ModalReestablecerPassword,
  },
  data() {
    return {
      isBusy: false,
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 50],
      sortBy: 'id',
      sortDesc: false,
      // sortDirection: 'desc',
      filter: '',
      filterOn: [],

    }
  },
  methods: {
    actualizarTablaProfesores() {
      this.$refs.refTablaProfesoresList.refresh()
    },
    agregarNuevoProfesor() {
      this.$emit('nuevoProfesor')
    },
    editarProfesor(item) {
      this.$emit('editarDatosProfesor', item)
    },
    restablecerContrasenia(item) {
      this.$emit('reestablecerPassword', item)
    },
    // eliminarUsuario(item) {
    //   this.$swal({
    //     title: '¿Esta seguro de eliminar al usuario?',
    //     text: 'Esta acción es irreversible',
    //     icon: 'warning',
    //     showCancelButton: true,
    //     confirmButtonText: 'Si, ¡Eliminar!',
    //     customClass: {
    //       confirmButton: 'btn btn-primary',
    //       cancelButton: 'btn btn-outline-danger ml-1',
    //     },
    //     buttonsStyling: false,
    //   }).then(result => {
    //     if (result.value) {
    //       store.dispatch('profesor-store-module/deleteUsuario', item.id).then(() => {
    //         this.$toast({
    //           component: ToastificationContent,
    //           position: 'top-right',
    //           props: {
    //             title: 'Usuario Eliminado!',
    //             icon: 'CheckCircleIcon',
    //             variant: 'success',
    //           },
    //         })
    //         this.actualizarTablaProfesores()
    //       })
    //         .catch(() => {
    //           this.$toast({
    //             component: ToastificationContent,
    //             position: 'top-right',
    //             props: {
    //               title: '¡Error al eliminar usuario!',
    //               icon: 'CheckCircleIcon',
    //               variant: 'danger',
    //             },
    //           })
    //         })
    //     }
    //   })
    // },
    async myProvider(ctx) {
      console.log('CTX: ', ctx)
      const promise = store.dispatch('profesor-store-module/getProfesores', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        console.log('responseGetIngresosMedicamentos: ', response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.to = to || 0
        /*
        items.forEach((value, index) => {
          items[index].fecha_venc_dias = this.fecha_menor_en_dias(value.lotes_disponibles)
        }) */

        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    },

    recorteName(name) {
      let recorte = []
      recorte = name.split(' ')
      return `${recorte[0]} ${recorte[1]}`
    },
  },
  setup() {
    const PROFESOR_APP_STORE_MODULE_NAME = 'profesor-store-module'

    // Register module
    if (!store.hasModule(PROFESOR_APP_STORE_MODULE_NAME)) store.registerModule(PROFESOR_APP_STORE_MODULE_NAME, profesorStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(PROFESOR_APP_STORE_MODULE_NAME)) store.unregisterModule(PROFESOR_APP_STORE_MODULE_NAME)
    })

    const fields = [
      { key: 'index', label: 'N°', sortable: true },
      { key: 'apellidos_nombres', label: 'APELLIDOS Y NOMBRES', sortable: true },
      { key: 'usuario', label: 'USUARIO', sortable: true },
      { key: 'estado', label: 'ESTADO', sortable: true },
      { key: 'acciones' },
    ]
    return {
      // Filter
      avatarText,
      fields,
      profesorStoreModule,
    }
  },
}
</script>
<style lang="scss">
// @import '@core/scss/vue/libs/vue-select.scss';
</style>
